//
// Author: Puru Choudhary (www.terminusapp.com)
// URL: https://github.com/medius/utm_cookie
//
// Description:
// This script saves UTM parameters in cookies whenever there are any UTM parameters
// in the URL. It also saves the initial referrer information in a cookie which is
// never (365 days) overwritten.
//
// Adding this script is useful for custom tracking. e.g. The values in the cookies
// can be read and added to forms or stored in the backend database, etc.
//

const utmCookie = {
  cookieNamePrefix: '_uc_',
  utmParams: [
    'utm_campaign',
    'utm_content',
    'utm_medium',
    'utm_source',
    'utm_term'
  ],
  cookieExpiryDays: 365,

  // From http://www.quirksmode.org/js/cookies.html
  createCookie (name, value, days) {
    let expires = ''
    if (days) {
      const msDay = 24 * 60 * 60 * 1000
      const date = new Date(Date.now() + days * msDay)
      expires = `expires="${date.toUTCString()}; `
    }
    document.cookie = `${this.cookieNamePrefix}${name}=${value}; ${expires}path=/`
  },

  readCookie (name) {
    const nameEQ = this.cookieNamePrefix + name + '='
    const ca = document.cookie.split(';')
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i]
      while (c.charAt(0) == ' ') c = c.substring(1, c.length)
      if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length)
    }
    return null
  },

  eraseCookie (name) {
    this.createCookie(name, '', -1)
  },

  getParameterByName (name) {
    name = name.replace(/[\[]/, '\\\[').replace(/[\]]/, '\\\]')
    const regexS = '[\\?&]' + name + '=([^&#]*)'
    const regex = new RegExp(regexS)
    const results = regex.exec(window.location.search)
    if (results === null) return ''
    return decodeURIComponent(results[1].replace(/\+/g, ' '))
  },

  utmPresentInUrl () {
    let present = false
    for (let i = 0; i < this.utmParams.length; i++) {
      const param = this.utmParams[i]
      const value = this.getParameterByName(param)
      if (value !== '' && value !== undefined) {
        present = true
      }
    }
    return present
  },

  writeUtmCookieFromParams () {
    for (let i = 0; i < this.utmParams.length; i++) {
      const param = this.utmParams[i]
      const value = this.getParameterByName(param)
      this.createCookie(param, value, this.cookieExpiryDays)
    }
  },

  writeCookieOnce (name, value) {
    const existingValue = this.readCookie(name)
    if (!existingValue) {
      this.createCookie(name, value, this.cookieExpiryDays)
    }
  },

  writeReferrerOnce () {
    let value = document.referrer
    if (value === "" || value === undefined) value = 'direct'
    this.writeCookieOnce("referrer", value)
  },

  referrer () {
    return this.readCookie("referrer")
  }
}

utmCookie.writeReferrerOnce()

if (utmCookie.utmPresentInUrl()) {
  utmCookie.writeUtmCookieFromParams()
}
