const view = document.querySelector('[data-element="thanks"]')

if (view) setTimeout(thanksInit, 0)

function thanksInit () {
  const text = view.querySelector('[data-element="thanks__text"]')
  const weekday = new Date().getDay()
  if (weekday === 0 || weekday === 6 ) {
    text.textContent = 'Наш менеджер перезвонит вам в понедельник утром'
  }
}
